$bgColor: white;
$bWidth: 1px;
$bColor: gainsboro;
$bRadius: 0.25rem;

.image-gallery {
  .image-gallery-content {
    .image-gallery-slide-wrapper {
      height: 300px;
      justify-content: center;
      align-items: center;
      display: flex;
      background-color: $bgColor;
      border: $bWidth solid $bColor;
      border-radius: $bRadius;
    }

    .image-gallery-thumbnails-wrapper {
      background-color: $bgColor;
      border: $bWidth solid $bColor;
      border-radius: $bRadius;
      margin-top: 10px;
    }
    .image-gallery-slide {
      .image-gallery-image {
        max-height: 300px !important;
        width: 100%;
        padding: 10px;
      }
    }
  }
}
